<template>
  <div>
    <div class="title">
      解决方案
    </div>
    <div class="WOBox">
        <!-- 问题显示 -->
        <div class="SDtit">
          问题内容
        </div>
        <el-card shadow="always" class="info">
          <el-button type="primary" @click="Fneditknowledge">{{display ? '隐藏问题':'显示问题'}}</el-button>

          <div v-show="display">
            <h1 class="detTitle">{{det.k_title}}</h1>
            <div class="infod">
                写作人<i>{{det.k_user_id}}</i>{{det.k_date}}
            </div>
            <div class="DetTontent" v-html="det.k_content"></div>
          </div>
        </el-card>
        <div class="SDtit">
          解决方案：
        </div>
      <div class="textBox">
        标题：<el-input v-model="input" class="inp" placeholder="请输入内容"></el-input>
        <!-- 富文本插件start -->
        <div ref="editor" style="text-align:left"></div>
        <!-- 富文本插件end -->
      </div>
      <el-button type="primary" @click="getContent">{{this.$route.query.k_status ? '修改':'保存'}}</el-button>
    </div>
  </div>
</template>
<script>
  import E from 'wangeditor'
  import url from "../../common/api";
  import { mapState } from 'vuex'
  export default {
    data() {
      return {
        activeName: 'first',
        editorContent:'',//这是富文本的内容
        editor:null,//这是富文本继承的方法挂载变量
        input:'',//这是标题
        det:{},//请求回来的问题内容
        getSolve:{},//修改的时候请求回来的参数
        display:false,//是否显示问题
      };
    },
    mounted() {
      // 富文本编辑内容开始
      this.editor = new E(this.$refs.editor)
      this.editor.customConfig.onchange = (html,a) => {
        this.editorContent = html
      }
      this.editor.customConfig.uploadImgServer =this.uploadimage   // 上传图片到服务器
      this.editor.customConfig.uploadFileName = 'image'
            this.editor.customConfig.uploadImgHooks = {  //监听
                customInsert: function (insertImg, result, editor) {
                    var url=result.ip + result.data[0]
                    insertImg(url)
                }
            }
      this.editor.customConfig.showLinkImg = false
      this.editor.create()
      // 富文本编辑内容结束
      console.log(this.$route.query,'---------路由传递过来的参数')
      // 判断是不是修改
      if(this.$route.query.k_status){
        // 调用详情赋值
        console.log('这是修改判断被调用------')
        this.Fnviewsolution()
      }
    },
    methods: {
      handleClick(tab, event) {//标签切换
        console.log(this.activeName,'--------------');
      },
      getContent(){//点击保存按钮触发的事件
        
        if(!this.$route.query.k_status){
          // 添加
          if(this.input!='' && this.editorContent!=''){
            url.addsolution({
              "user_id": this.buserInfo.user_id,
              "s_k_id": this.$route.query.s_k_id,
              "s_title": this.input,
              "s_type": "知识库",
              "s_content": this.editorContent
            }).then(res=>{
              if(res.data.code){
                this.$message(res.data.message[0]);
                this.$router.push('/bkIndex/bkknowledge')
              }
            })        
          }else{
            this.$message.error('标题和问题内容不能为空');
          }
        }else{
          // 调用修改接口
          if(this.input!='' && this.editorContent!=''){
            url.Pviewsolution({
              "user_id": this.buserInfo.user_id,
              "s_k_id": this.getSolve.s_k_id,
              "s_id": this.getSolve.s_id,
              "s_title": this.input,
              "s_type": "知识库",
              "s_content": this.editorContent
            }).then(res=>{
              if(res.data.code){
                this.$message(res.data.message[0]);
                this.$router.push('/bkIndex/bkknowledge')
              }
              console.log(res,'------------修改接口')
            })       
          }else{
            this.$message.error('标题和问题内容不能为空');
          }
        }

        console.log(this.editorContent,'----------------------')
      },
      Fneditknowledge(){//查看单个的知识库详情
          this.display=!this.display
          if(this.display){
            url.editknowledge({
                k_id:this.$route.query.s_k_id
            })
            .then(res=>{
                console.log(res,'---------查看详情')
                if(res.data.code[0]='200'){
                    let xx=res.data.data[0].fields
                    xx.k_id=res.data.data[0].pk
                    this.det=xx
                }
            })
          }

      },
      Fnviewsolution(){//查看单个的知识库**解决方案详情**
        // console.log(this.$route.query.s_k_id,'----------cuo')
        url.viewsolution({
          s_k_id:this.$route.query.s_k_id
        }).then(res=>{
          console.log(res,'s_id======单个解决方案详情')
          if(res.data.code[0]='200'){
              let xx=res.data.data[0].fields
              xx.s_id=res.data.data[0].pk
              this.getSolve=xx
              this.editor.txt.html(xx.s_content)
              this.input=xx.s_title
          }
        })
      }
    },
    computed:{
      ...mapState(['static','uploadimage','buserInfo'])
    }
  };
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
.WOBox
    padding 30px
    .SDtit
      font-size 30px
      margin 30px 0
    .textBox
        width 950px
        height 667px
        margin-bottom 20px
        .inp
          font-size 24px
          margin-bottom 15px
          >>> .el-input__inner
            border none
.textBox >>> .w-e-text-container
  min-height 580px !important

.detTitle
    font-size: 22px;
    line-height: 1.4;
    margin-bottom: 14px;
.infod
    font-size 14px
    color #999
    i 
        margin 0 10px
        color #576b95
.DetTontent
    color #595757
    font-size 15px
    letter-spacing 1px
    line-height 1.6
    margin-top 25px
    img 
        max-width 700px
        display block
        margin 0 auto
    .logo
        width 90px
        height 90px
    p 
        text-indent 2em
</style>


